namespace $.$$ {

	export class $ss_linkpad2_link_default extends $.$ss_linkpad2_link_default {

		actions(): ReturnType< $ss_linkpad2['actions'] > {
			throw new Error( "should be overriden by $ss_linkpad2" )
		}
		get_data(): ReturnType< $ss_linkpad2['get_data'] > {
			throw new Error( "should be overriden by $ss_linkpad2" )
		}

		// type_data(): $ss_linkpad2_link_data {
		// 	return this.Link_data( this.data().type_id() )
		// }
		type_name(): string {
			return this.type_data()?.name()
		}
		type_name_before(): string {
			return `"${this.type_name()?.replaceAll('\n',' ')}"`
		}
		color() {
			return `hsl(${this.type_data()?.hue()} 80% 60%)`
		}
		semitransporent() {
			return `hsl(${this.type_data()?.hue()} 90% 60% / 20%)`
		}
		name() {
			return this.data().name()
		}
		has_name(): boolean {
			return Boolean( this.name() )
		}
		focuslineover( next?: any ) {
		}

		@ $mol_mem
		block_id(): $ss_linkpad2_link_block_id {
			return [ this.ui_id(), 'value' ]
		}

		@ $mol_mem
		block_dom_id(): string {
			return this.Value_block().dom_id()
		}

		Value_block() {
			const block = this.Block( this.block_id() )
			block.value_changed = next => this.data().val( next )
			block.update_value( this.data().val() )
			// block.on_arrow_down = (e:KeyboardEvent) => {
			// 	this.Links().dom_node_actual().setAttribute( 'contenteditable', 'false' )
			// 	setTimeout(() => {
			// 		this.Links().dom_node_actual().setAttribute( 'contenteditable', '' )
			// 	}, 100);
			// }
			block.on_blur = ()=> {
			}

			block.on_focus = ()=> {
				this.on_focus( this.ui_id() )
			}

			block.delete_backward = (e: KeyboardEvent)=> {
				//if pos != 0
				//just get text from above and insert
				const value_above = this.get_data().sibling_above_value( this.ui_id() )
				if( value_above === null ) return
				if( ! (value_above == '' || value_above == '\n') ) block.insert_in_sel( value_above )
				this.actions().remove_sibling_above( this.ui_id() )
			}

			block.custom_keydown = (e: KeyboardEvent)=> {
				if( e.key == 'Tab' ) {
					e.preventDefault()
					if( e.shiftKey ) {
						this.actions().move_below_parent( this.ui_id() )
					} else {
						this.actions().move_inside_upper_sibling( this.ui_id() )
					}
				}
			}

			block.keydown_enter = (e: KeyboardEvent)=> {
				e.preventDefault()
				if(e.shiftKey) {
					document.execCommand( 'insertLineBreak' )
					return true
				}
				document.execCommand( 'insertLineBreak' )
				const sel = document.getSelection()
				let nextEl = sel?.focusNode as HTMLElement
				let next = ''
				while( nextEl ) {
					next += nextEl?.textContent
					nextEl = nextEl.nextSibling as HTMLElement
				}

				let prevEl = sel?.focusNode?.previousSibling as HTMLElement
				let prev = ''
				while( prevEl ) {
					prev = prevEl?.textContent + prev
					prevEl = prevEl.previousSibling as HTMLElement
				}
				if( prev == '' && sel?.focusOffset != 0 ) { //in end
					const temp_prev = prev
					prev = next
					next = temp_prev
				}
				this.data().val( next )

				block.update_value( next )
				this.actions().new_sibling_above( this.ui_id(), $ss_blocks_block_trim(prev) || '\n' , { focus: false } )
				block.focus( 'start' )

				return true

				// below
				// let nextEl = sel?.focusNode as HTMLElement
				// let next = ''
				// while( nextEl ) {
				// 	next += nextEl?.textContent
				// 	nextEl = nextEl.nextSibling as HTMLElement
				// }

				// let prevEl = sel?.focusNode?.previousSibling as HTMLElement
				// let prev = ''
				// while( prevEl ) {
				// 	prev = prevEl?.textContent + prev
				// 	prevEl = prevEl.previousSibling as HTMLElement
				// }
				// if( prev == '' && sel?.focusOffset != 0 ) { //in end
				// 	const temp_prev = prev
				// 	prev = next
				// 	next = temp_prev
				// }
				// this.data().val( prev )
				// console.log('prev', JSON.stringify( prev))
				// console.log('next', JSON.stringify( next))
				// block.update_value( $ss_blocks_block_trim(prev) || '\n' )
				// const new_line = this.actions().new_sibling_down( this.ui_id(), next, { focus: prev == '\n' ? false : true } )
				// return true
			}
			// block.placeholder = ()=> 'value'
			return block
		}

		focused(): boolean {
			return this.Value_block().focused()
		}

		auto() {
			this.hovered()
			this.depth()
		}

		@ $mol_mem
		hovered( ): boolean {
			if( this.self_hovered() ) {
				const event = this.mouseenter()
				this.on_hovered( this.ui_id(), event )
			}
			return this.self_hovered()
		}

		links_view(): readonly any[] {
			return this.links().length > 0 ? [
				this.Links()
			] : []
		}

		to_link_view(): readonly any[] {
			return this.to_link()?.links().length > 0 ? [
				this.To_link()
			] : []
		}
		
		fold_click( next?: any ) {
			this.fold( this.ui_id() )
		}

	}
	
}

