namespace $.$$ {
	
	export class $ss_linkpad2_link_hover extends $.$ss_linkpad2_link_hover {
		
		@ $mol_mem
		mouseenter( event? : MouseEvent ) {
			$mol_wire_solid()
			this.hovered( true )
			return event
		}
		
		@ $mol_mem
		mouseleave( event? : MouseEvent ) {
			$mol_wire_solid()
			this.hovered( false )
			return event
		}

	}

}
