namespace $ {

	const { vary } = $mol_style_func

	const block_width_vw = 80
	const block_width_max_px = block_width_vw * 8

	export const $ss_linkpad2_theme = {
		block_width_vw,
		block_width: vary('--ss_linkpad2_theme_block_width', `min(${block_width_vw}vw, ${block_width_max_px}px)`),
		block_padding: vary('--ss_linkpad2_theme_block_width', `min(${block_width_vw / 10}vw, ${block_width_max_px / 10}px)`),
		block_width_px: ()=> {
			const w = document.documentElement.clientWidth
			const block_w = w * block_width_vw/100
			return Math.min( block_w, block_width_max_px )
		},
	}

}
